<template>
    <div>
        <v-container fluid>
            <v-row>
                <v-col cols="12">
                    <span> Title: {{ dataMail.title }} </span>
                </v-col>
                <v-col cols="12">
                    <span> Name: {{ dataMail.name }} </span>
                </v-col>
                <v-col>
                    <span> Message: </span> <br>
                    <span> {{ dataMail.message }} </span>
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    name: "ReadMail",

    mounted() {
        this.setMailMessage(this.$route.params.slug);
    },

    computed: {
        ...mapGetters({
            dataMail: "getMailMessage",
        })
    },

    methods: {
        ...mapActions({
            setMailMessage: "setMailMessage"
        }),
    },

    metaInfo() {
        return {
            titleTemplate: "%s | " + this.dataMail.title,
            meta: [
                {
                    vmid: "title",
                    name: "title",
                    content: this.dataMail.title,
                },
                {
                    vmid: "description",
                    name: "description",
                    content: "bar",
                },
            ],
        };
    },
};
</script>

